:root {
  --color: #ffe9e9;
  --color-dark: #e0c4c4;
  --color-border: rgba(80, 57, 57, 0.45);
  --color-text: rgb(80, 57, 57);
}

.App {
  text-align: center;
  overflow: hidden;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

