.c-button {
  background: var(--color);
  border-radius: 3em;
  border: 0;
  border: 3px solid var(--color-border);
  color: var(--color-text);
  cursor: pointer;
  font-weight: bold;
  outline: none;
  padding: 1.25em 2em;
  position: relative;
  text-transform: uppercase;
  transform-style: preserve-3d;
  transition: all 150ms cubic-bezier(.175, .885, .32, 1.275);
}

.c-button::before {
  background: var(--color-dark);
  border-radius: inherit;
  box-shadow:
    0 0 0 2px var(--color-border),
    0 .6em 0 0 rgba(var(--color), .6);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transform: translate3d(0, .75em, -1em);
  transition: all 150ms cubic-bezier(.175, .885, .32, 1.275);
  width: 100%;
}

.c-button:hover {
  transform: translate(0, .25em);
}

.c-button:hover::before {
  box-shadow:
    0 0 0 2px var(--color-border),
    0 .45em 0 0 rgba(var(--color), .8);
  transform: translate3d(0, .5em, -1em);
}

.c-button:active {
  background: var(--color);
  transform: translate(0, .75em);
}

.c-button:active::before {
  box-shadow:
    0 0 0 3px var(--color-border),
    0 0 var(--color);
  transform: translate3d(0, 0, -1em);
}

.App-header main {
  display: none;
}

video::-webkit-media-controls-panel {
  display: none !important;
  opacity: 1 !important;
}

video::-webkit-media-controls {
  display: none;
}